import React, { Component, Fragment } from "react";

export class User extends Component {
  componentDidMount() {
    this.props.getUser(this.props.match.params.login);
  }
  render() {
    const {
      name,
      avatar_url,
      location,
      bio,
      blog,
      login,
      html_url,
      followers,
      following,
      public_repos,
      public_gists,
      hireable
    } = this.props.user;
    return (
      <Fragment>
        <img
          src={avatar_url}
          alt="Avatar"
          style={{ height: "500px", width: "500px", display: "block" }}
        />
        <h1 style={{ display: "inline" }}>{name} </h1>
        <h4 style={{ display: "inline" }}>({login})</h4>
        <h2>{bio}</h2>
        <p>Location: {location}</p>
        <p>Followers: {followers}</p>
        <p>Following: {following}</p>
        <p>Public Repos: {public_repos}</p>
        <p>Public Gists: {public_gists}</p>
        <p>Looking for a job: {hireable ? "Yes" : "No"}</p>
        <p>{html_url}</p>
        <p>{blog}</p>
      </Fragment>
    );
  }
}

export default User;
